@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/DateTime";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Form/InputRadio";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

.linkAccount {
    > header {
        @include Border.set($radius-tl: beta, $radius-tr: beta);

        & #{Text.$selector} {
            @include Font.set($size: delta, $weight: bold);
        }
    }

    > section {
        & #{Form.$selector} {
            @include Margin.set($top: alpha);

            > header {
                @include Flex.set($justify: between);
                @include Padding.set($top: alpha, $right: alpha, $bottom: alpha, $left: zeta);
                @include Color.set($bg: "gray-400");
                width: Layout.get-max-width(alpha);
                #{Text.$selector} {
                    @include Font.set($size: gamma, $weight: bold, $family: heading);
                    @include Color.set($fg: "gray-dark");
                }
            }

            > section {
                #{InputRadio.$selector} {
                    @include Margin.set($left: alpha);
                    svg {
                        @include Flex.set($shrink: none);
                    }
                    > div > div {
                        width: Layout.get-max-width(alpha);

                        .accountInfo {
                            @include Padding.set($left: alpha, $right: alpha);
                            @include Flex.set($align: center, $justify: between);

                            > div {
                                @include Flex.set($dir: column);
                                .accountType#{Text.$selector} {
                                    @include Font.set($weight: bold);
                                }
                            }

                            #{AmountFormatter.$selector} {
                                > span {
                                    @include Color.set($fg: "black");
                                }
                            }

                            #{Text.$selector} {
                                @include Font.set($transform: caps);
                                @include Flex.set($align: center);
                                @include Color.set($fg: "gray-dark");
                            }
                        }
                    }
                }
            }

            footer {
                #{Button.$selector} {
                    @include Margin.set($top: gamma, $bottom: gamma);
                }
            }
        }
    }

    .withoutAccounts {
        #{Button.$selector} {
            @include Margin.set($top: gamma, $bottom: gamma);
        }
    }
}

.keepAccounts {
    @include Padding.set($preset: beta);
    @include Flex.set($dir: column, $grow: alpha);
    @include Margin.set($top: gamma);
    @include Border.set($radius-tl: delta, $radius-tr: delta);
    @include Shadow.set($box: gamma);

    header {
        .title {
            @include Display.set($display: flex);
            @include Margin.set($bottom: beta);

            &#{Text.$selector} {
                @include Font.set($size: iota, $weight: bold);
            }
        }
    }

    section {
        @include Margin.set($bottom: delta);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta);
        }

        .subtitle#{Text.$selector} {
            @include Margin.set($bottom: beta);
        }
    }

    footer {
        #{Button.$selector} {
            @include Margin.set($bottom: beta);
        }
    }
}

.linkAccountDetail {
    @include Flex.set($dir: column, $grow: alpha);

    section {
        ul {
            li {
                @include Display.set($display: grid);
                @include Margin.set($top: beta, $bottom: beta);
                grid-template-columns: 1fr 1fr;
                #{Text.$selector} {
                    @include Font.set($size: gamma);
                    @include Font.set($space: nowrap);
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                #{Text.$selector}:last-child,
                #{DateTime.$selector}:last-child {
                    @include Font.set($align: right, $weight: medium);
                    @include Font.set($weight: medium);
                    @include Color.set($fg: "gray-dark");
                }
            }
        }
    }
}
